@import url('https://fonts.googleapis.com/css2?family=Recursive&display=swap');

* {
  font-family: 'Recursive', sans-serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  padding: 0 6rem;
}

h1,
h2,
h3 {
  font-weight: bold;
  font-size: 3.5rem;

}

input {
  width: 100%;
  color: gray;
  padding: .7rem;
  border-radius: 1rem;
  border: 1px solid #000000
}

.card-grid {
  max-width: 120rem;
  margin: 0 auto;
}

@media (min-width: 460px) {
  .card-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem;
  }

}

@media (min-width: 768px) {
  .card-grid {
    grid-template-columns: repeat(5, 1fr);
  }

}

.card {
  border: 1px solid grey;
  border-radius: .6rem;
  margin-bottom: 1rem;
  text-transform: capitalize;
}

.card-content {
  max-width: 50rem;
  max-height: 10rem;

}

.card img {
  height: 20rem;
  width: 100%;
}

.card p {
  padding: .5rem;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}